div.icon-container {
    position: relative;
    top: 2px;
    left: 2px;
}
summary::-webkit-details-marker {
    color: rgb(178, 148, 187);
    height: 10px;
    width: 8px;
}

*:focus {
    outline: 0;
}

details.logGroup summary::-webkit-details-marker {
    display: none;
}

details.logGroup > summary:first-of-type {
    list-style-type: none;
}

details.logGroup[open] .logGroupSummaryClosed {
    display: none;
}

details.logGroup .logGroupSummaryClosed {
    display: inline-block;
}

details.logGroup .logGroupSummaryOpen {
    display: none;
}

details.logGroup[open] .logGroupSummaryOpen {
    display: inline-block;
}

/*details.logGroup > summary > *,*/
/*details.logGroup > summary > * > *,*/
/*details.logGroup > summary > * > * > * {*/
/*    background-color: rgb(43, 52, 62) !important;*/
/*}*/

details.because[open] .summaryClosed {
    display: none;
}

details.because .summaryClosed {
    display: inline-block;
}

details.because .summaryOpen {
    display: none;
}

details.because[open] .summaryOpen {
    display: inline-block;
}

details.because summary::-webkit-details-marker {
    display: none;
}

details.because > summary:first-of-type {
    list-style-type: none;
}

.row:after {
    content: "";
    display: table;
    clear: both;
}

@font-face {
    font-family: 'Averia Sans Libre';
    font-style: normal;
    font-weight: 400;
    src: url('/mockserver/dashboard/AveriaSansLibre-Regular.ttf');
    src: url('/mockserver/dashboard/AveriaSansLibre-Regular.woff2') format('woff2'), url('/mockserver/dashboard/AveriaSansLibre-Regular.ttf')  format('truetype');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

